<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="col-md-12 p-0">
            <div class="tab-content">                
                <dynamic-form lang="en" ref="AddConfig" :buttons="buttons" :schema="FormSchema"
                    v-on:OnSubmit="onSubmit" :mandatorytext="$t('MandatoryString')">
                </dynamic-form>                                  
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../../../services/AdvanceSetupModuleService';
export default {    
    data() {
        return {
            UserData: [],            
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('User'),
                            name: "User",
                            value: "",
                            placeholder: this.$t('User'),
                            config: {
                                options: [],
                                onChange: this.onChangeProfile,
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "ToggleField",
                            label: this.$t('CanViewAllCrmRecord'),
                            name: "CanViewAllCrmRecord",
                            value: "Yes",
                            placeholder: "",
                            validationRules: { "required": false },
                        },
                    ]
                }
            ],
            isLoading:false,
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                }
            ],
        }
    },
    created:function(){
        var vm= this;
        vm.GetUser();
    },
    methods: {
        onSubmit: function (formfields) {
            var vm = this;
            vm.isLoading = true;
            var obj = {
                User: formfields.User,
                CanViewAllCrmRecord: formfields.CanViewAllCrmRecord
            }            
            var params = JSON.stringify(obj);
            DataService.SaveConfigData(params).then(response => {
                if (response.data.result == "INSERT") {
                    vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Configuration')]), "success", true, vm.$t("Alert"));
                } else if (response.data.result == "UPDATE") {
                    vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Configuration')]), "success", true, vm.$t("Alert"));
                }
                else {
                    vm.ShowAlert(vm.$t('AddedError', [vm.$t('Configuration')]), "danger", true, vm.$t("Alert"));
                }                
                vm.isLoading = false;
            });
        },
        GetUser: function () {
            var vm = this;
            var param = 'moduleName=CRM_USER&type=USERS';
            DataService.GetDDLDataForAddGroup(param).then(response => {
                vm.UserData = response.data.DATA;
                vm.UserData.forEach((item, indx) => {
                    vm.FormSchema[0].Data[0].config.options.push({
                        name: item.name,
                        value: item.value
                    })
                })
                vm.$refs.AddConfig.UpdateKeyValue();
            });
        }
    }
}
</script>
