<template lang="en">
    <div class="border p-3">
        <dynamic-form ref="AddlocationRef" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
    </div>
    </template>

<script>

import assetDataService from "@/services/AdvanceSetupModuleService";
import commonDataService from "../../../../services/DataService";

// import SearchSechma from "../../views/AdvancedSetup/SearchSchema"

 //import  TreeSelectField  from "tg-controls_cli/dist/myLib.common";

 import TreeSelectField from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";

export default {
  props: {
    popupCondition: {
      type: Boolean,
      required: true,
    },
    tabCount: 0,
    currentTabIndex: 0
  },




  components: {
     TreeSelectField,
  },
  data() {
    return {
      FormSchema: [
        {
          layoutType: "triple",
          Data: [
            {
              astype: "TreeSelectField",
              label: this.$t("ParentLocation"),
              name: "Location",
              value: null,
              placeholder: "Select Location",
              config: {
                options: [],
              },
              validationRules: {
                required: false,
              },
            },
            {
              astype: "TextField",
              label: this.$t("LocationName"),
              name: "LocationName",
              value: "",
              placeholder: "",
              validationRules: {
                required: true,
                max: 250,
              },
            },
            {
              astype: "SelectField",
              label: this.$t("Status"),
              name: "Status",
              value: "",
              placeholder: "",
              validationRules: {
                required: true,
                max: 250,
              },
              config: {
                options: [],
                onChange: () => { },
              },
            },
            {
              astype: "TextField",
              label: this.$t("AddressLine1"),
              name: "AddressLine1",
              value: "",
              placeholder: "",
              validationRules: {
                required: true,
                max: 250,
              },
            },
            {
              astype: "TextField",
              label: this.$t("City"),
              name: "City",
              value: "",
              placeholder: "",
              validationRules: {
                required: true,
                max: 250,
              },
            },
            {
              astype: "SelectField",
              label: this.$t("Country"),
              name: "Country",
              value: "",
              placeholder: "",
              validationRules: {
                required: true,
                max: 250,
              },
              config: {
                // option_request_url: process.env.VUE_APP_COMMON_API + '/Common/GetDDLData?moduleName=CRM&type=COUNTRIES',
                // token: this.GetUserInfo.Token,
                options: [],
                onChange: this.onChangeDropdown,
              },
            },
            {
              astype: "SelectField",
              label: this.$t("State"),
              name: "State",
              value: "",
              placeholder: "",
              config: {
                options: [],
              },
            },
            {
              astype: "TextField",
              label: this.$t("PostalCode"),
              name: "PostalCode",
              value: "",
              placeholder: "",
            },
            {
              astype: "TextField",
              label: this.$t("Email"),
              name: "Email",
              value: "",
              placeholder: "",
            },
            {
              astype: "TextField",
              label: this.$t("Phone1"),
              name: "Phone1",
              value: "",
              placeholder: "",
            },
            {
              astype: "TextField",
              label: this.$t("Phone2"),
              name: "Phone2",
              value: "",
              placeholder: "",
            },
            {
              astype: "TextField",
              label: this.$t("LocationCode"),
              name: "LocationCode",
              value: "",
              placeholder: "",
            },
            {
              astype: "SelectField",
              label: this.$t("Tag"),
              name: "Tag",
              value: "",
              placeholder: "",
              config: {
                options: [],
              },
            },
          ],
        },
      ],
      buttons: [
        {
          type: "button",
          class: "popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4",
          text: this.$t('Skip'),
          isVisible: (this.tabCount > this.currentTabIndex) ? true : false,
          onClick: this.Skip
        },

        {
          type: "submit",
          class: "btn btn-success",
          text: "<i class='fa fa-save pr-2'></i>Save & New",
          name: "SaveAndNew",
          isVisible: false,
        },
        {
          type: "submit",
          class: "btn btn-success",
          text: "<i class='fa fa-save pr-2'></i>Save & Next",
          name: "SaveAndNext",
          isVisible: false,
        },
        {
          type: "submit",
          class: "btn btn-success",
          text: "<i class='fa fa-save pr-2'></i>Save And Next",
          name: "Save",
        },

      ],
      myObj: [],
      LocationData: [],
      CountryData: [],
      StatusData: [
        {
          name: "Active",
          value: 1001,
        },
        {
          name: "Inactive",
          value: 1002,
        },
      ],
      StateData: [],
      TagData: [],
      CreatedAt: "",
      CreatedBy: "",
      ModifiedAt: "",
      ModifiedBy: "",
      DataLoaded: 0,
      UserId: "",
      SetupProgresText: "",
      FindalLocationArray: [],
    };
  },

  created: async function () {
    var vm = this;
    await vm.GetLocationByCompanyId();
    vm.GetStatusDDL();
    vm.GetCountryDDL();
    await vm.GetTagsByModuleId();
    await vm.AdvancedSetupFields()
    vm.UserId = vm.GetUserInfo.ID;
    setTimeout(() => {
      vm.$refs.AddlocationRef.UpdateKeyValue();
    }, 200);
    //vm.DataLoaded = 1;
  },
  methods: {

    Skip: function () {
      debugger;

      var vm = this;
      vm.$parent.NextTab();
    },
    async AdvancedSetupFields() {
      var vm = this;
      if (vm.popupCondition == "setup") {
        vm.FormSchema[0].Data[6].visibility = false;
        vm.FormSchema[0].Data[7].visibility = false;
        vm.FormSchema[0].Data[8].visibility = false;
        vm.FormSchema[0].Data[9].visibility = false;
        vm.FormSchema[0].Data[10].visibility = false;
        vm.FormSchema[0].Data[11].visibility = false;
        vm.FormSchema[0].Data[12].visibility = false;
        vm.buttons[0].isVisible = true;
        vm.buttons[1].isVisible = false;
        vm.buttons[2].isVisible = true;
        vm.buttons[3].isVisible = false;
        vm.buttons[4].isVisible = false;
      }
    },
    GetCountryDDL: function () {
      debugger;
      var url = `moduleName=CRM&type=COUNTRIES`;
      commonDataService.GetCountryDDL(url).then((result) => {
        result.data.DATA.forEach((item) => {
          this.FormSchema[0].Data[5].config.options.push({
            name: `${item.name}`,
            value: `${item.value}`,
          });
        });
        // this.$refs.AddlocationRef.UpdateKeyValue();
      });
    },
    async GetTagsByModuleId() {
      var vm = this;
      var params = `modulename=Asset`;
      await assetDataService.GetTagsByModuleId(params).then(function (response) {
        vm.TagData = JSON.parse(response.data);
        vm.TagData.data.forEach((itm, index) => {
          vm.FormSchema[0].Data[12].config.options.push({
            name: itm.TAG_NAME,
            value: itm.TAG_ID,
          });
        });
        console.log(" GetTagsByModuleId  call end");
      });
    },
    onChangeDropdown(evt) {
      var vm = this;
      var params = `countryId=${evt.target.value}`;
      assetDataService.GetStateListwithIDByCountryId(params).then(function (
        response
      ) {
        vm.StateData = JSON.parse(response.data).data;
        vm.StateData = vm.StateData.map((item) => {
          return {
            name: item.name,
            value: item.state_id,
          };
        });
        $.each(vm.FormSchema, function (index, item) {
          $.each(item.Data, function (index, element) {
            if (element.name == "State") {
              element.value = "";
              element.config.options = vm.StateData;
            }
          });
        });
        vm.$refs.AddlocationRef.UpdateKeyValue();
      });
    },
    async GetLocationByCompanyId() {
      debugger
      var vm = this;
      vm.isLoading = true;
      var params = `reqForm=`;
      await assetDataService.GetLocationByCompanyIdAsset(params).then(function (
        response
      ) {
        vm.FindalLocationArray = [];
        vm.LocationData = JSON.parse(response.data).data;
        //var json =vm.MakeNormalArray(response.data).data;
        vm.LocationData.forEach((value) => {
          var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
            return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
          });
          if (CheckIfExist.length == 0) {
            if (value.PARENT_LOCATION_ID == null) {
              let locationId = value.COMPANY_LOCATION_ID;
              let rslt = vm.getChildLocation(
                locationId,
                value,
                JSON.parse(response.data).data
              );
              vm.FindalLocationArray.push(rslt);
            }
          }
        });
        vm.FormSchema[0].Data[0].config.options = vm.FindalLocationArray;
      });
    },
    getChildLocation: function (locationId, currentItem, data) {
      let ths = this;
      let treeArray = {};
      treeArray = {
        id: currentItem.COMPANY_LOCATION_ID,
        label: currentItem.LOCATION_NAME,
      };
      let retrunArray = treeArray;
      var childArray = data.filter(function (el) {
        return el.PARENT_LOCATION_ID == locationId;
      });
      let finalChildArray = [];
      childArray.forEach((value) => {
        let ChildfolderId = value.COMPANY_LOCATION_ID;
        var CheckIfExist1 = finalChildArray.filter(function (el) {
          return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
        });
        if (CheckIfExist1.length == 0) {
          var CheckIfExist = data.filter(function (el) {
            return el.PARENT_LOCATION_ID == ChildfolderId;
          });
          if (CheckIfExist != null && CheckIfExist.length > 0) {
            let rslt = ths.getChildLocation(ChildfolderId, value, data);
            finalChildArray.push(rslt);
          } else {
            treeArray = {
              id: `${value.COMPANY_LOCATION_ID}`,
              label: `${value.LOCATION_NAME}`,
            };
            finalChildArray.push(treeArray);
          }
        }
      });
      retrunArray.children = finalChildArray;
      return retrunArray;
    },
    GetStatusDDL() {
      var vm = this;
      vm.StatusData.forEach((item, index) => {
        vm.FormSchema[0].Data[2].config.options.push({
          name: item.name,
          value: item.value,
        });
      });
    },
    ClosePopup() {
      var vm = this;
      vm.$parent.CloseLocationPopup();
    },

    onSubmit(e, val) {
      var vm = this;
      var propValues = e;
      var url = `LocationName=${e.LocationName}&LocationId=0`;
      assetDataService.LocationExist(url).then(function (response) {
        vm.LocationId = response.data.result;
        if (response.data.result > 0) {
          vm.ShowAlert(
            vm.$t("Location already exist", [vm.$t("Location")]),
            "warning",
            true,
            vm.$t("Alert")
          );
        } else {
          let myObj = {
            LocationId: vm.LocationId,
            LocationName: e.LocationName,
            Country: parseInt(e.Country),
            State: e.State,
            Address1: e.AddressLine1,
            Address2: "",
            City: e.City,
            PinCode: e.PostalCode,
            Email: e.Email,
            Phone1: e.Phone1,
            Phone2: e.Phone2,
            CreatedAt: null,
            CreatedBy: vm.UserId,
            ModifiedAt: null,
            ModifiedBy: vm.UserId,
            StatusId: e.Status,
            State_id: e.State == "" ? null : e.State,
            LocationCode: e.LocationCode,
            ParentLocationId: e.Location == "" ? null : e.Location,
            TagId:
              e.Tag.length <= 0
                ? null
                : Array.prototype.map
                  .call(e.Tag, function (item) {
                    return item.value;
                  })
                  .join(","),
            CampusId: "",
            ClientId: 1,
          };
          var jsonObj = JSON.stringify(myObj);
          assetDataService.SaveUpdateLocation(myObj).then(function (response) {
            if (val == "SaveAndNew") {
              if (response.data == "1" || response.data == null) {
                vm.ShowAlert(
                  vm.$t("Location has been added successfully ", [
                    vm.$t("Location"),
                  ]),
                  "success",
                  true,
                  vm.$t("Alert")
                );
                vm.resetForm();
              }
            } else if (val == "SaveAndNext") {
              if (response.data == "1" || response.data == null) {
                vm.ShowAlert(
                  vm.$t("Location has been added successfully ", [
                    vm.$t("Location"),
                  ]),
                  "success",
                  true,
                  vm.$t("Alert")
                );

                
                if (vm.popupCondition == "setup") {
                  // SearchSechma.GetProgresSetupStatusDetail(this, "Asset");
                  vm.$parent.GetProgresSetupStatusDetail();
                }
                if (vm.tabCount > 1) {
                        vm.$emit("nextTab")
                        vm.$emit("updateProgress", currentTabIndex, true);
                    }
               // vm.$parent.SkipTab(2);
              }
            } else if (val == "Save") {
              if (response.data == "1" || response.data == null) {
                vm.ShowAlert(
                  vm.$t("Location has been added successfully ", [
                    vm.$t("Location"),
                  ]),
                  "success",
                  true,
                  vm.$t("Alert")
                );
                if (vm.tabCount > 1) {
                        vm.$emit("nextTab")
                        vm.$emit("updateProgress", currentTabIndex, true);
                    }
              }
            } else if (response.data == "2") {
              vm.ShowAlert(
                vm.$t("Location has been updated successfully", [
                  vm.$t("Location"),
                ]),
                "success",
                true,
                vm.$t("Alert")
              );
            } else if (response.data == "0") {
              vm.ShowAlert(
                vm.$t("unknown error", [vm.$t("Location")]),
                "success",
                true,
                vm.$t("Alert")
              );
            }
            // confuse in data resposne but saved location
            else if (response.data == null) {
              vm.ShowAlert(
                vm.$t("Location has been added successfully", [
                  vm.$t("Location"),
                ]),
                "success",
                true,
                vm.$t("Alert")
              );
            }
            if (vm.popupCondition == "setup") {
              //SearchSechma.GetProgresSetupStatusDetail(this, "Asset");
              vm.$parent.GetProgresSetupStatusDetail();
            }
            //vm.$parent.CloseLocationPopup();
          });
        }
      });
    },
    resetForm() {
      this.FormSchema[0].Data.forEach((field) => {
        if (field.astype == "TreeSelectField") {
          field.value = null;
        } else {
          field.value = "";
          field.validationRules.required = true;
        }
      });
    },
  },
};
</script>
