<template>
    <section>
        <div class="col-md-12 p-0">
            <div class="tab-content">
                <dynamic-form lang="en" ref="AddLead" :buttons="buttons" :schema="FormSchema"
                    v-on:OnSubmit="onSubmit" :mandatorytext="$t('MandatoryString')"></dynamic-form>               
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../../../services/AdvanceSetupModuleService'
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0
    },
    data() {
        return {
            LeadData: [],
            username: null,
            userid: null,
            isLoading: false,           
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('FirstName'),
                            name: "FirstName",
                            value: "",
                            placeholder: this.$t('FirstName'),
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('LastName'),
                            name: "LastName",
                            value: "",
                            placeholder: this.$t('LastName'),
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('LeadEmail'),
                            name: "LeadEmail",
                            value: "",
                            placeholder: this.$t('LeadEmail'),
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('LeadOwner'),
                            name: "LeadOwner",
                            value: "",
                            placeholder: this.$t('LeadOwner'),
                            config: {
                                options: [],
                                onChange: this.onChangeProfile,
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('LeadDate'),
                            name: "LeadDate",
                            value: new Date(),
                            placeholder: this.$t('LeadDate'),
                            validationRules: {
                                "required": true
                            },
                            config: { mode: 'date', format: "M/D/YYYY" }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "1001",
                            placeholder: this.$t('Status'),
                            config: {
                                options: [{ name: 'Active', value: 1001 }, { name: 'InActive', value: '1002' }],
                                onChange: this.onChangeProfile,
                            },
                            validationRules: {
                                "required": true
                            }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Country'),
                            name: "Country",
                            value: "",
                            placeholder: this.$t('Country'),
                            config: {
                                options: [],
                                onChange: this.onChangeProfile,
                            },
                            validationRules: {
                                "required": true
                            }
                        },
                    ]
                }
            ],
            buttons: [
                 {
                    type: "button",
                    class: "popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4",
                    text: "Skip",
                    isVisible: false,
                    onClick: this.Skip
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                }
            ],
        }
    },
    created: function () {
        this.username = this.GetUserInfo.Name;
        this.userid = this.GetUserInfo.ID;
        this.buttons[0].isVisible = (this.tabCount  > this.currentTabIndex) ? true : false
        this.GetCountryData();
        this.GetLeadOwner();
    },
    methods: {
        GetCountryData: function () {
            var vm = this;
            DataService.GetCountryData().then(function (response) {
                var data = response.data;
                data.forEach(element => {
                    vm.FormSchema[0].Data[6].config.options.push({
                        name: element.countrY_FULL_NAME,
                        value: element.id
                    })
                });
                vm.$refs.AddLead.UpdateKeyValue();
            })
        },
        GetLeadOwner: function () {
            var vm = this;
            vm.FormSchema[0].Data[3].config.options.push({
                name: vm.username,
                value: vm.userid
            })
        },
        onSubmit: function (formfields) {
            var vm = this;
            vm.isLoading = true;
            var obj = {
                "first_name": formfields.FirstName,
                "last_name": formfields.LastName,
                "email": formfields.LeadEmail,
                "lead_owner": formfields.LeadOwner,
                "lead_date": formfields.LeadDate,
                "status_id": formfields.Status,
                "country": formfields.Country
            }
            var params = obj;
            DataService.AddLead(params).then(function (response) {
                if (response != null && response.data[0].Status == "Success") {
                    vm.ShowAlert(vm.$t("RecordsSavedSuccessfully"));
                    if (vm.tabCount > 1) {
                        vm.$emit("nextTab")
                    }
                    else {
                        vm.clearFields();
                    }
                }
            })
            vm.isLoading = false;
        },
        clearFields: function () {
            var vm = this;            
            vm.FormSchema[0].Data[0].value="";
            vm.FormSchema[0].Data[1].value="";
            vm.FormSchema[0].Data[2].value="";
            vm.FormSchema[0].Data[3].value="";
            vm.FormSchema[0].Data[4].value="";
            vm.FormSchema[0].Data[5].value="";
            vm.FormSchema[0].Data[6].value="";
            vm.$refs.validatePriority.validateForm.reset(); 
        },
        Skip: function () {
            var vm = this;
            vm.$parent.ShowNextTab(2);
        }
    }
}
</script>